.radioButton {
    /**    **/
    padding: 14px 0;

    /**  **/
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 20px;

    /**   **/
    outline: none;
}

.radioButton:first-child {
    /**   **/
    border-radius: 6px 0 0 6px;
}

.radioButton:last-child {
    /**   **/
    border-radius: 0 6px 6px 0;
}

.on {
    /**   **/
    background-color: #EBF0FA;

    /**   **/
    border: 1px solid #CAD8F0;

    /**    **/
    color: #175CD3;
}

.off {
    /**   **/
    background-color: #FFFFFF;

    /**   **/
    border: 1px solid #E4E7EC;

    /**    **/
    color: #98A2B3;
}
