.container {
    /**    **/
    width: 1366px;

    /**    **/
    margin: 0 auto;
    margin-top: 130px;
    margin-bottom: 90px;

    /**    **/
    padding: 0 103px;

    /**   **/
    display: grid;
    grid-template-columns: 250px auto;
    column-gap: 30px;
    row-gap: 26px;

    .title {
        /**    **/
        grid-column-start: 1;
        grid-column-end: 3;

        /**   **/
        font-style: normal;
        font-weight: bold;
        font-size: 24px;
        line-height: 30px;
        color: #101828;
    }

    .side {
        .card {
            /**   **/
            background-color: #FFFFFF;

            /**   **/
            border: 1px solid #E4E7EC;
            border-radius: 6px;

            .info {
                /**    **/
                padding: 20px;

                /**   **/
                display: grid;
                row-gap: 20px;

                .top {
                    /**   **/
                    display: grid;
                    grid-template-columns: 80px auto;
                    justify-content: space-between;
                }

                .bottom {
                    /**   **/
                    display: grid;
                    row-gap: 8px;

                    > h3 {
                        /**   **/
                        font-style: normal;
                        font-weight: bold;
                        font-size: 16px;
                        line-height: 20px;
                        color: #101828;
                    }

                    > p {
                        /**   **/
                        font-style: normal;
                        font-weight: normal;
                        font-size: 14px;
                        line-height: 18px;
                        color: #98A2B3;
                    }
                }
            }

            .linkList {
                /**   **/
                display: grid;
                row-gap: 28px;

                /**    **/
                padding: 34px 20px;

                /**   **/
                border-top: 1px solid #E4E7EC;
                border-bottom: 1px solid #E4E7EC;

                > a {
                    /**   **/
                    font-style: normal;
                    font-weight: normal;
                    font-size: 16px;
                    line-height: 20px;
                    color: #101828;
                }

                .selected {
                    /**   **/
                    font-weight: bold;

                    /**    **/
                    cursor: default;
                }
            }

            .logout {
                /**    **/
                padding: 15px 20px;

                > span {
                    /**   **/
                    font-style: normal;
                    font-weight: normal;
                    font-size: 14px;
                    line-height: 18px;
                    color: #98A2B3;
                }
            }
        }
    }
}