@import '../../../css/common/zIndex';

.overlays {
    /**    **/
    width: 100%;
    height: 100%;

    /**    **/
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    /**   **/
    background-color: rgba(0, 0, 0, 0.3);

    /**    **/
    z-index: $modalZIndex;

    /**     **/
    cursor: pointer;

    .modal {
        /**    **/
        width: 520px;

        /**    **/
        position: fixed;
        top: calc(50% - (382px / 2));
        left: calc(50% - (520px / 2));
        
        /**   **/
        background-color: #FFFFFF;

        /**   **/
        border-radius: 6px;

        /**    **/
        padding: 30px;

        /**   **/
        display: grid;
        row-gap: 34px;

        /**     **/
        cursor: default;

        .content {
            /**   **/
            display: grid;
            row-gap: 20px;

            .top {
                /**   **/
                display: grid;
                grid-template-columns: auto auto;
                justify-content: space-between;

                .profile {
                    /**   **/
                    display: grid;
                    grid-template-columns: 50px auto;
                    column-gap: 12px;

                    .circle_50 {
                        /**    **/
                        width: 50px;
                        height: 50px;
                        box-sizing: border-box;

                        /**   **/
                        background: #F9FAFB;

                        /**   **/
                        border: 1px solid #E4E7EC;
                        border-radius: 25px;

                        /**   **/
                        font-style: normal;
                        font-weight: normal;
                        font-size: 14px;
                        line-height: 18px;
                        color: #101828;

                        /**    **/
                        text-align: center;

                        /**    **/
                        padding: 15px 0;
                    }

                    .info {
                        /**   **/
                        display: grid;
                        row-gap: 6px;

                        /**    **/
                        margin: 7px 0;

                        .txt {
                            /**   **/
                            font-style: normal;
                            font-weight: normal;
                            font-size: 12px;
                            line-height: 15px;
                            color: #101828;
                        }
                    }
                }

                .closeBtn {
                    /**    **/
                    width: 32px;
                    height: 32px;

                    /**    **/
                    margin: 9px 0;

                    /**    **/
                    padding: 4px;
                }
            }

            .study {
                /**   **/
                display: grid;
                row-gap: 10px;

                /**    **/
                padding: 14px 15px;

                .title {
                    /**   **/
                    font-style: normal;
                    font-weight: 500;
                    font-size: 16px;
                    line-height: 20px;
                    color: #101828;
                }

                .position {
                    /**   **/
                    font-style: normal;
                    font-weight: normal;
                    font-size: 16px;
                    line-height: 20px;
                    color: #101828;
                }
            }

            .introudctuion {
                /**   **/
                font-style: normal;
                font-weight: normal;
                font-size: 16px;
                line-height: 24px;
                color: #101828;
            }
        }

        .btnSection {
            /**   **/
            display: grid;
            grid-template-columns: auto auto;
            column-gap: 10px;
        }
    }
}