.container {
    /**    **/
    width: 158px;
    height: 40px;

    /**   **/
    display: grid;
    grid-template-columns: 40px 78px 40px;

    button {
        /**   **/
        background-color: #FFFFFF;

        /**   **/
        border: 1px solid #E4E7EC;

        /**    **/
        padding: 10px;
    }

    .removeBtn {
        /**   **/
        border-radius: 6px 0 0 6px;
    }

    .addBtn {
        /**   **/
        border-radius: 0 6px 6px 0;
    }

    .input {
        /**   **/
        background-color: #FFFFFF;
        
        /**   **/
        border-top: 1px solid #E4E7EC;
        border-bottom: 1px solid #E4E7EC;

        /**   **/
        display: grid;
        grid-template-columns: auto auto;
        column-gap: 4px;
        justify-content: center;

        /**    **/
        padding: 10px 0;

        > input {
            /**    **/
            display: inline-block;

            /**    **/
            width: 20px;
            height: 20px;

            /**   **/
            font-style: normal;
            font-weight: normal;
            font-size: 16px;
            line-height: 20px;
            color: #101828;

            /**    **/
            text-align: center;

            /**   **/
            border: none;

            /**   **/
            outline: none;
        }

        > span {
            /**    **/
            width: 20px;
            height: 20px;

            /**   **/
            font-style: normal;
            font-weight: normal;
            font-size: 16px;
            line-height: 20px;
            color: #101828;
        }
    }
}