.container {
    /**   **/
    display: grid;
    row-gap: 20px;

    .selectSection {
        /**   **/
        display: grid;
        row-gap: 10px;
    }

    .positionSection {
        /**   **/
        background: #F9FAFB;

        .txt {
            /**    **/
            display: block;

            /**   **/
            background: #F9FAFB;

            /**    **/
            padding: 10px 0;

            /**   **/
            border-top: 1px solid #E4E7EC;
            border-bottom: 1px solid #E4E7EC;

            /**   **/
            font-style: normal;
            font-weight: normal;
            font-size: 14px;
            line-height: 18px;
            color: #98A2B3;

            /**    **/
            text-align: center
        }

        .position {
            /**   **/
            display: grid;
            grid-template-columns: auto 26px;
            column-gap: 20px;

            /**    **/
            padding: 20px 15px;

            /**   **/
            border-top: 1px dashed #E4E7EC;

            .content {
                /**   **/
                display: grid;
                grid-template-columns: auto auto;
                justify-content: space-between;
                column-gap: 40px;

                > div {
                    /**   **/
                    display: grid;
                    row-gap: 10px;
                    h3 {
                        /**   **/
                        font-style: normal;
                        font-weight: normal;
                        font-size: 16px;
                        line-height: 20px;
                        color: #101828;
                    }

                    .skillTags {
                        /**   **/
                        font-style: normal;
                        font-weight: normal;
                        font-size: 16px;
                        line-height: 20px;
                        color: #98A2B3;
                    }
                }
            }

            .closeBtn {
                /**    **/
                width: 26px;
                height: 26px;

                /**   **/
                background-color: #FFFFFF;

                /**   **/
                border: 1px solid #E4E7EC;
                border-radius: 4px;
            }
        }

        .position:first-child {
            /**   **/
            border: none;
        }
    }
}