.container {
    /**    **/
    width: 1366px;

    /**    **/
    margin: 0 auto;
    margin-top: 130px;
    margin-bottom: 90px;

    /**   **/
    background-color: #FFFFFF;

    /**    **/
    padding: 0 103px;

    /**   **/
    display: grid;
    grid-template-columns: 790px auto;
    column-gap: 30px;

    .content {
        /**   **/
        display: grid;
        row-gap: 60px;

        .title {
            /**    **/
            padding-bottom: 40px;

            /**   **/
            border-bottom: 1px solid #E4E7EC;

            input {
                /**    **/
                width: 790px;

                /**   **/
                font-style: normal;
                font-weight: normal;
                font-size: 32px;
                line-height: 40px;
                color: #101828;

                /**   **/
                border: none;

                /**   **/
                outline: none;
            }

            input::placeholder {
                /**    **/
                color: #98A2B3;
            }
        }

        .body {
            /**   **/
            display: grid;
            row-gap: 40px;

            .section {
                /**   **/
                display: grid;
                row-gap: 8px;

                h3 {
                    /**   **/
                    font-style: normal;
                    font-weight: normal;
                    font-size: 16px;
                    line-height: 20px;
                    color: #667085
                }

                .profileContent {
                    /**   **/
                    display: grid;
                    grid-auto-flow: column;
                    column-gap: 20px;
                    justify-content: flex-start;

                    .txt {
                        /**   **/
                        font-style: normal;
                        font-weight: normal;
                        font-size: 16px;
                        line-height: 20px;
                        color: #98A2B3;
                    }
                }

                .durationContent {
                    /**   **/
                    display: grid;
                    grid-template-columns: 293px auto 293px auto;
                    column-gap: 15px;

                    .txtContent {
                        /**   **/
                        display: grid;
                        align-items: center;
                        
                        /**    **/
                        margin-bottom: 8px;

                        .txt {
                            /**   **/
                            font-style: normal;
                            font-weight: normal;
                            font-size: 16px;
                            line-height: 20px;
                            color: #101828;
                        }
                    }
                }
            }
        }
    }

    .side {
        /**    **/
        position: relative;

        .card {
            /**    **/
            width: 340px;

            /**   **/
            background-color: #FFFFFF;

            /**   **/
            box-shadow: 0px 8px 30px rgba(16, 24, 40, 0.02), 
            0px 8px 24px rgba(16, 24, 40, 0.02), 
            0px 4px 12px rgba(16, 24, 40, 0.02), 
            0px 2px 6px rgba(16, 24, 40, 0.02);

            /**   **/
            border-radius: 6px;

            /**    **/
            padding: 20px;

            /**    **/
            position: fixed;

            /**   **/
            display: grid;
            row-gap: 10px;
        }
    }
}