@import '../../../css/common/zIndex';

.overlays {
    /**    **/
    width: 100%;
    height: 100%;

    /**    **/
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    /**   **/
    background-color: rgba(0, 0, 0, 0.3);

    /**    **/
    z-index: $modalZIndex;

    /**     **/
    cursor: pointer;

    .modal {
        /**    **/
        width: 520px;
        height: 369px;

        /**    **/
        position: fixed;
        top: calc(50% - (369px / 2));
        left: calc(50% - (520px / 2));

        /**   **/
        background-color: #FFFFFF;

        /**   **/
        border-radius: 6px;

        /**   **/
        border-radius: 6px;

        /**    **/
        padding: 30px;

        /**     **/
        cursor: default;

        .closeBtn {
            float: right;

            /**    **/
            padding: 4px;

            /**    **/
            width: 32px;
            height: 32px;
        }

        .emotionImg {
            /**    **/
            position: relative;
            top: 10px;
            left: 185px;
        }

        h3 {
            /**   **/
            font-style: normal;
            font-weight: bold;
            font-size: 24px;
            line-height: 30px;
            color: #101828;

            /**    **/
            text-align: center;

            /**    **/
            margin-top: 25px; 
        }

        p {
            /**   **/
            font-style: normal;
            font-weight: normal;
            font-size: 16px;
            line-height: 24px;
            color: #667085;

            /**    **/
            text-align: center;

            /**    **/
            margin-top: 8px;
        }

        .studyInfo {
            /**   **/
            background-color: #F9FAFB;

            /**   **/
            border-radius: 6px;

            /**    **/
            margin-top: 20px;

            /**    **/
            padding: 14px 15px;

            /**   **/
            display: grid;
            row-gap: 10px;

            h3 {
                /**   **/
                font-style: normal;
                font-weight: 500;
                font-size: 16px;
                line-height: 20px;
                color: #101828;

                /**    **/
                text-align: left;

                /**    **/
                margin: 0;
            }

            p {
                /**   **/
                font-style: normal;
                font-weight: normal;
                font-size: 16px;
                line-height: 20px;
                color: #101828;

                /**    **/
                text-align: left;

                /**    **/
                margin: 0;
            }
        }

        .profileInfo {
            /**   **/
            background-color: #F9FAFB;

            /**   **/
            border-radius: 6px;

            /**    **/
            margin-top: 20px;

            /**    **/
            padding: 14px 15px;

            /**   **/
            display: grid;
            grid-template-columns: 50px auto;
            column-gap: 12px;

            .circle_50 {
                /**    **/
                width: 50px;
                height: 50px;
                box-sizing: border-box;

                /**   **/
                background: #F9FAFB;

                /**   **/
                border: 1px solid #E4E7EC;
                border-radius: 25px;

                /**   **/
                font-style: normal;
                font-weight: normal;
                font-size: 14px;
                line-height: 18px;
                color: #101828;

                /**    **/
                text-align: center;

                /**    **/
                padding: 15px 0;
            }

            .txt {
                /**   **/
                display: grid;
                row-gap: 6px;

                /**    **/
                padding: 7px 0;

                p {
                    /**   **/
                    font-style: normal;
                    font-weight: normal;
                    font-size: 12px;
                    line-height: 15px;
                    color: #101828;

                    /**    **/
                    text-align: left;

                    /**      **/
                    margin: 0;
                    padding: 0;
                }
            }
        }
    }
}