.container {
    /**    **/
    width: 790px;
    box-sizing: border-box;

    /**    **/
    position: relative;

    .select {
        /**   **/
        background-color: #FFFFFF;

        /**   **/
        border: 1px solid #E4E7EC;
        border-radius: 6px;

        /**    **/
        padding: 10px 15px;

        /**   **/
        display: grid;
        grid-template-columns: auto 24px;
        column-gap: 15px;
        justify-content: space-between;

        /**     **/
        cursor: pointer;

        .txt {
            /**   **/
            font-style: normal;
            font-weight: normal;
            font-size: 16px;
            line-height: 20px;
            color: #98A2B3;

            /**    **/
            padding: 3px 0;
        }

        img {
            /**    **/
            padding: 1px 2px;
        }
    }

    .options {
        /**    **/
        width: 790px;
        box-sizing: border-box;
        
        /**   **/
        background-color: #FFFFFF;

        /**   **/
        border: 1px solid #E4E7EC;
        border-radius: 6px;

        /**   **/
        display: grid;
        row-gap: 15px;

        /**    **/
        position: absolute;
        top: 56px;

        /**    **/
        z-index: 100;

        .content {
            /**    **/
            padding: 20px 0;

            /** Flex  **/
            display: flex;
            justify-content: space-evenly;

            .leftSection {
                /**    **/
                width: 30px;

                > button {
                    /**    **/
                    float: left;

                    /**    **/
                    position: relative;
                    left: 20px;
                }
            }

            .rightSection {
                /**    **/
                width: 29px;

                > button {
                    /**    **/
                    float: right;

                    /**    **/
                    position: relative;
                    right: 20px;
                }
            }

            .calendarSection {    
                /**   **/
                display: grid;
                grid-template-columns: 336px 336px;
                column-gap: 59px;
                justify-content: flex-start;
            }
        }

        .btnSection {
            /**   **/
            display: grid;
            justify-content: flex-end;

            /**    **/
            padding: 0 15px 15px 0;
        }
    }
}