.container {
    /**    **/
    position: absolute;

    /**    **/
    z-index: 60;

    /**    **/
    width: 100%;
    
    /**   **/
    background-color: #FFFFFF;

    /**   **/
    border: 1px solid #E4E7EC;
    border-radius: 6px;

    .group {
        /**   **/
        border-bottom: 1px solid #E4E7EC;

        .title {
            /**    **/
            padding: 14px;
    
            /**   **/
            background-color: #F2F4F7;
    
            /**   **/
            border: none;
    
            /**   **/
            font-style: normal;
            font-weight: normal;
            font-size: 14px;
            line-height: 18px;
            color: #667085;
        }
    
        .option {
            /**    **/
            padding: 10px 15px;
    
            /**   **/
            border-top: 1px solid #E4E7EC;
    
            /**   **/
            font-style: normal;
            font-weight: normal;
            font-size: 16px;
            line-height: 20px;
            color: #101828;

            /**     **/
            cursor: pointer;
        }
    }

    .group:last-child {
        /**   **/
        border: none;
    }
}