@import '../../../css/common/zIndex';

.overlays {
    /**    **/
    width: 100%;
    height: 100%;

    /**    **/
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    /**   **/
    background-color: rgba(0, 0, 0, 0.3);

    /**    **/
    z-index: $modalZIndex;

    /**     **/
    cursor: pointer;

    .modal {
        /**    **/
        width: 520px;
        height: 338px;

        /**   **/
        background-color: #FFFFFF;

        /**   **/
        border-radius: 6px;

        /**    **/
        position: fixed;
        top: calc(50% - (338px / 2));
        left: calc(50% - (520px / 2));

        /**     **/
        cursor: default;

        img {
            /**    **/
            margin: 40px 215px 0 215px;;
        }

        h3 {
            /**   **/
            font-style: normal;
            font-weight: bold;
            font-size: 24px;
            line-height: 30px;
            color: #101828;

            /**    **/
            margin: 25px 90px 0 90px;
        }

        p {
            /**   **/
            font-style: normal;
            font-weight: normal;
            font-size: 16px;
            line-height: 25px;
            color: #101828;

            /**    **/
            margin: 10px 103px 0 104px;
        }

        .btnSection {
            /**    **/
            margin: 40px 30px 30px 30px;

            /**   **/
            display: grid;
            grid-template-columns: 225px 225px;
            column-gap: 10px;
        }
    }
}