.container {
    /**    **/
    display: inline-block;

    /**    **/
    position: relative;

    /**    **/
    width: 40px;
    height: 22px;

    /**     **/
    cursor: pointer;

    .switch {
        /**    **/
        position: absolute;

        /**    **/
        width: 22px;
        height: 22px;

        /**   **/
        border-radius: 11px;

        /**   **/
        box-shadow: 0px 8px 30px rgba(16, 24, 40, 0.02), 
        0px 8px 30px rgba(16, 24, 40, 0.02),
        0px 8px 24px rgba(16, 24, 40, 0.02), 
        0px 4px 12px rgba(16, 24, 40, 0.02), 
        0px 2px 6px rgba(16, 24, 40, 0.02);

        /**    **/
        z-index: 10;

        /**   **/
        background-color: #FFFFFF;
    }

    .selectedBtn {
        /**    **/
        left: 22px;
    }

    .noSelectedBtn {
        /**    **/
        left: 0px;
    }

    .background {
        /**    **/
        position: absolute;

        /**    **/
        width: 40px;
        height: 18px;

        /**    **/
        margin: 2px 0;

        /**   **/
        border-radius: 20px;
    }

    .noSelectedBg {
        /**   **/
        background-color: #E4E7EC;
    }

    .selectedBg {
        /**   **/
        background-color: #175CD3;
    }
}