.container {
    /**    **/
    position: absolute;

    /**    **/
    width: 100%;

    /**    **/
    padding: 15px;

    /**   **/
    background-color: #FFFFFF;

    /**   **/
    border: 1px solid #E4E7EC;
    border-radius: 6px;

    /**    **/
    z-index: 60;

    ul {
        /**   **/
        display: grid;
        grid-template-columns: auto auto auto;
        grid-gap: 14px;
    }
}