.container {
    /**   **/
    display: grid;
    row-gap: 6px;

    .textInputContainer {
        /**   **/
        border-radius: 6px;
    
        input {
            /**    **/
            padding: 14px 0 14px 15px;
        
            /**   **/
            font-style: normal;
            font-weight: normal;
            font-size: 16px;
            line-height: 20px;
            color: #101828;;
        
            /**   **/
            outline: none;
        
            /**   **/
            border: none;
            border-radius: 6px;
        
            /**   **/
            background-color: inherit;
        }
        
        input[type="text"]::placeholder {
            /**   **/
            font-style: normal;
            font-weight: normal;
            font-size: 16px;
            line-height: 20px;
            color: #98A2B3;;
        }
        
        input[type="text"]:disabled {
            /**   **/
            background-color: #F9FAFB;
        
            /**   **/
            font-style: normal;
            font-weight: normal;
            font-size: 16px;
            line-height: 20px;
            color: #98A2B3;
        }

        button {
            /**   **/
            outline: none;
        }
        
        .textInputButton {
            /**   **/
            background-color: inherit;
        
            /**    **/
            padding: 14px 15px;
        
            /**   **/
            border-left: 1px solid #E4E7EC;
            border-radius: 0 6px 6px 0;
        
            /**   **/
            font-style: normal;
            font-weight: normal;
            font-size: 16px;
            line-height: 20px;
            color: #175CD3;
        }
    
    }

    .default {
        /**   **/
        background-color: #FFFFFF;
    
        /**   **/
        border: 1px solid #E4E7EC;
    }
    
    .success {
        /**   **/
        background-color: #E8F0FE;
    
        /**   **/
        border: 1px solid #E4E7EC;
    }
    
    .error {
        /**   **/
        background-color: #FFFFFF;
    
        /**   **/
        border: 1px solid #F36355;
    }
    
    .helpText {
        /**   **/
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 18px;
        color: #667085;
        letter-spacing: -0.02em;
    }

    .errorText {
        /**   **/
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 18px;
        color: #F36355;
        letter-spacing: -0.02em;
    }
}