.container {
    /**   **/
    display: grid;
    grid-auto-flow: column;
    column-gap: 10px;
    justify-content: flex-start;
    
    .btn {
        /**   **/
        border-radius: 6px;

        /**    **/
        width: 18px;
        height: 18px;

        /**    **/
        padding: 5px 4px;

        /**   **/
        display: flex;
    }

    .button_off {
        /**   **/
        background-image: url(../../../images/icon/ic_check_off_18dp.svg);
        background-repeat: no-repeat;
        background-position: center;

        /**   **/
        background-color: #FFFFFF;

        /**   **/
        border: 1px solid #E4E7EC;
    }

    .button_on {
        /**   **/
        background-image: url(../../../images/icon/ic_check_on_18dp.svg);
        background-repeat: no-repeat;
        background-position: center;

        /**   **/
        background-color: #1A5DD1;

        /**   **/
        border: none;
    }

    > span {
        /**   **/
        font-style: normal;
        font-weight: normal;
        font-size: 16px;
        line-height: 20px;
        letter-spacing: -0.02em;
    }

    .span_on {
        /**    **/
        color: #175CD3;
    }

    .span_off {
        /**    **/
        color: #98A2B3;
    }
}