.container {
    /**   **/
    display: grid;
    row-gap: 8px;

    /**    **/
    position: relative;

    .select {
        /**   **/
        background-color: #FFFFFF;

        /**   **/
        border: 1px solid #E4E7EC;
        border-radius: 6px;

        /**    **/
        padding: 12px 15px;

        /**   **/
        font-style: normal;
        font-weight: normal;
        font-size: 16px;
        line-height: 20px;

        /**   **/
        display: grid;
        grid-template-columns: calc(100% - 24px) 24px;
        justify-content: space-evenly;

        /**     **/
        cursor: pointer;

        span {
            /**    **/
            padding: 2px 0;

            /**   **/
            display: block;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
        }
    }

    .options {
        /**    **/
        position: relative;

        /**    **/
        z-index: 60;

        background-color: #FFFFFF;
    }
}