.container {
    /**   **/
    background-color: #FFFFFF;

    /**   **/
    box-shadow: 0px 8px 24px rgba(16, 24, 40, 0.02), 
    0px 4px 12px rgba(16, 24, 40, 0.02), 
    0px 2px 6px rgba(16, 24, 40, 0.02);

    /**   **/
    border-radius: 6px;

    /**    **/
    padding: 20px;
    
    /**   **/
    display: grid;

    .content {
        /**   **/
        display: grid;
        grid-template-columns: auto 89px;
        column-gap: 22px;

        /**    **/
        padding-bottom: 15px;

        /**   **/
        border-bottom: 1px dashed #E4E7EC;

        .section {
            /**   **/
            display: grid;
            grid-auto-flow: column;
            justify-content: space-between;

            /**    **/
            padding: 10px 0;

            .txt {
                /**   **/
                font-style: normal;
                font-weight: normal;
                font-size: 16px;
                line-height: 20px;
                color: #101828;
            }
        }
    }

    .skillTags {
        /**   **/
        display: grid;
        grid-auto-flow: column;
        grid-gap: 12px;
        justify-content: flex-start;

        /**    **/
        padding-top: 10px;

        .tag {
            /**   **/
            font-style: normal;
            font-weight: normal;
            font-size: 16px;
            line-height: 20px;
            color: #98A2B3;
            letter-spacing: -0.02em;
        }

        .tag::before {
            /**   **/
            content: '#';
        }
    }
}