.container {
    /**   **/
    display: grid;
    grid-auto-flow: column;
    column-gap: 18px;
    justify-content: flex-start;

    .tab {
        /**   **/
        font-style: normal;
        font-size: 18px;
        line-height: 23px;

        /**    **/
        padding-bottom: 4px;
    }

    .selected {
        /**   **/
        font-weight: bold;
        color: #175CD3;

        /**   **/
        border-bottom: 2px solid #175CD3;

        /**     **/
        cursor: default;
    }

    .noSelected {
        /**   **/
        font-weight: normal;
        color: #101828;

        /**     **/
        cursor: pointer;
    }
}