.container {
    /**    **/
    width: 1270px;

    /**    **/
    margin: 0 auto;

    /**   **/
    display: grid;
    grid-template-columns: 40px 1160px 40px;
    column-gap: 15px;

    .side {
        display: grid;
        align-items: center;
        button {
            /**    **/
            padding: 8px
            ;

            /**   **/
            border: none;

            /**   **/
            background-color: #FFFFFF;
        }
    }

    .content {
        /**    **/
        width: 1160px;

        /**      **/
        overflow: hidden;

        ul {
            /**    **/
            width: 4640px;

            /**   **/
            display: grid;
            grid-template-columns: calc(1120px / 3) calc(1120px / 3) calc(1120px / 3)
            calc(1120px / 3) calc(1120px / 3) calc(1120px / 3)
            calc(1120px / 3) calc(1120px / 3) calc(1120px / 3)
            calc(1120px / 3) calc(1120px / 3) calc(1120px / 3);
            column-gap: 20px;
        }
    }

}