.container {
    /**   **/
    background-color: #FFFFFF;

    /**   **/
    border-radius: 6px;

    /**   **/
    box-shadow: 0px 8px 30px rgba(16, 24, 40, 0.02), 
    0px 8px 24px rgba(16, 24, 40, 0.02), 
    0px 4px 12px rgba(16, 24, 40, 0.02), 
    0px 2px 6px rgba(16, 24, 40, 0.02);

    /**    **/
    padding: 20px 20px 23px 20px;

    /**   **/
    display: grid;
    grid-template-columns: auto 24px;
    column-gap: 16px;

    .content {
        /**   **/
        display: grid;
        row-gap: 19px;

        .circle_50 {
            /**    **/
            width: 50px;
            height: 50px;
            box-sizing: border-box;

            /**   **/
            border: 1px solid #E4E7EC;
            border-radius: 25px;

            /**   **/
            background-color: #F9FAFB;

            img {
                /**    **/
                width: 50px;
                height: 50px;

                /**   **/
                border-radius: 25px;
            }
        }

        .text {
            /**    **/
            text-align: center;
            padding: 14px 0;

            /**   **/
            font-style: normal;
            font-weight: normal;
            font-size: 16px;
            line-height: 20px;
            color: #101828;
        }

        .detail {
            /**   **/
            display: grid;
            row-gap: 14px;

            .top {
                /**   **/
                display: grid;
                row-gap: 4px;

                .name {
                    /**   **/
                    font-style: normal;
                    font-weight: bold;
                    font-size: 16px;
                    line-height: 28px;
                    color: #101828;
                }

                .position {
                    /**   **/
                    font-style: normal;
                    font-weight: normal;
                    font-size: 16px;
                    line-height: 20px;
                    color: #101828;

                    /**   **/
                    display: block;
                    white-space: nowrap;
                    overflow: hidden;
                    text-overflow: ellipsis;
                }
            }

            .skillTags {
                /**   **/
                font-style: normal;
                font-weight: normal;
                font-size: 16px;
                line-height: 20px;
                color: #98A2B3;

                /**   **/
                display: block;
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
            }
        }
    }

    .bookMarkSection {
        button {
            padding: 0;
        }
    }
}