@import '../../../css/common/zIndex';

.overlays {
    /**    **/
    width: 100%;
    height: 100%;

    /**    **/
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    /**   **/
    background-color: rgba(0, 0, 0, 0.3);

    /**    **/
    z-index: $modalZIndex;

    /**     **/
    cursor: pointer;

    .modal {
        /**    **/
        width: 520px;
        height: 272px;

        /**    **/
        position: fixed;
        top: calc(50% - (272px / 2));
        left: calc(50% - (520px / 2));

        /**   **/
        background-color: #FFFFFF;

        /**   **/
        border-radius: 6px;

        /**    **/
        padding: 30px;

        /**     **/
        cursor: default;

        .titleBar {
            /**   **/
            display: grid;
            grid-auto-flow: column;
            justify-content: space-between;

            h3 {
                /**   **/
                font-style: normal;
                font-weight: bold;
                font-size: 24px;
                line-height: 30px;
                color: #101828;

                /**    **/
                padding-top: 1px;
            }

            button {
                /**    **/
                width: 32px;
                height: 32px;

                /**    **/
                padding: 4px;
            }
        }

        .studyKindSection {
            /**    **/
            margin-top: 34px;

            /**   **/
            display: grid;
            grid-template-columns: auto auto;
            row-gap: 30px;

            .btn {
                /**   **/
                font-style: normal;
                font-weight: normal;
                font-size: 16px;
                line-height: 20px;

                /**    **/
                padding: 24px;
            }

            .completeBtn {
                /**    **/
                grid-column-start: 1;
                grid-column-end: 3;

                /**   **/
                display: grid;
            }

            .btn:first-child {
                /**   **/
                border-radius: 6px 0 0 6px;
            }

            .btn:last-child {
                /**   **/
                border-radius: 0 6px 6px 0;
            }

            .on {
                /**   **/
                background-color: #EBF0FA;

                /**    **/
                color: #175CD3;

                /**   **/
                border: 1px solid #CAD8F0;
            }

            .off {
                /**   **/
                background-color: #FFFFFF;

                /**    **/
                color: #98A2B3;

                /**   **/
                border: 1px solid #E4E7EC;
            }
        }
    }
}