.container {
    /**   **/
    background-color: #FFFFFF;

    /**   **/
    display: grid;
    row-gap: 25px;
    
    h3 {
        /**   **/
        font-style: normal;
        font-weight: bold;
        font-size: 18px;
        line-height: 23px;
        color: #101828;

        /**    **/
        text-align: center;
    }

    .content {
        /**   **/
        display: grid;
        row-gap: 10px;

        .weeks {
            /**   **/
            display: grid;
            grid-template-columns: 48px 48px 48px 48px 48px 48px 48px;

            > span {
                /**   **/
                font-style: normal;
                font-weight: normal;
                font-size: 14px;
                line-height: 18px;
                color: #98A2B3;

                /**    **/
                text-align: center;
            }
        }

        .days {
            /**   **/
            display: grid;
            grid-template-columns: 48px 48px 48px 48px 48px 48px 48px;

            .day {
                /**   **/
                font-style: normal;
                font-weight: normal;
                font-size: 16px;
                line-height: 20px;
                color: #101828;

                /**    **/
                padding: 14px 12px;
            }

            .day:disabled {
                /**   **/
                background-color: #FFFFFF;

                /**    **/
                color: #FFFFFF;

                /**     **/
                cursor: default;
            }

            .selected {
                /**   **/
                background-color: #175CD3;

                /**    **/
                color: #FFFFFF;
            }

            .interval {
                /**   **/
                background-color: #EBF0FA;
            }
        }
    }
}