.container {
    /**   **/
    display: grid;
    row-gap: 6px;

    .textarea {
        /**   **/
        background-color: #FFFFFF;

        /**   **/
        outline: none;

        /**   **/
        border: 1px solid #E4E7EC;
        border-radius: 6px;

        /**     **/
        resize: none;

        /**   **/
        font-style: normal;
        font-weight: normal;
        font-size: 16px;
        line-height: 26px;
        color: #101828;

        /**    **/
        padding: 14px 15px;
    }

    .textarea::placeholder {
        /**   **/
        font-style: normal;
        font-weight: normal;
        font-size: 16px;
        line-height: 26px;
        color: #98A2B3;
    }

    .textarea::-webkit-scrollbar {
        width: 13px;
    }

    .textarea::-webkit-scrollbar-thumb {
        background-color: #98A2B3;
        border-radius: 6px;
    }

    span {
        /**   **/
        font-style: normal;
        font-weight: normal;
        font-size: 12px;
        line-height: 15px;
        color: #667085;
    }
}