.container {
    /**    **/
    width: 1366px;

    /**    **/
    margin: 0 auto;
    margin-top: 130px;
    margin-bottom: 90px;

    /**    **/
    padding: 0 103px;

    /**   **/
    display: grid;
    row-gap: 30px;

    div {
        /**   **/
        display: grid;
        grid-template-columns: auto auto auto auto auto;
        row-gap: 30px;

        a {
            /**   **/
            font-style: normal;
            font-weight: bold;
            font-size: 24px;
            line-height: 32px;
            color: #101828;

            /**    **/
            text-decoration: underline;
        }
    }
}