.container {
    /**    **/
    position: absolute;

    /**    **/
    width: 100%;

    /**   **/
    background-color: #FFFFFF;

    /**   **/
    border: 1px solid #E4E7EC;
    border-radius: 6px;

    /**    **/
    padding: 5px 0;

    /**    **/
    z-index: 60;

    .option {
        /**    **/
        padding: 10px 15px;

        /**   **/
        border-bottom: 1px solid #E4E7EC;

        /**   **/
        font-style: normal;
        font-weight: normal;
        font-size: 16px;
        line-height: 20px;
        color: #101828;

        /**     **/
        cursor: pointer;
    }

    .option:last-child {
        /**   **/
        border: none;
    }

    .placeholder {
        /**    **/
        padding: 10px 15px;

        /**   **/
        background-color: #FFFFFF;

        /**   **/
        font-style: normal;
        font-weight: normal;
        font-size: 16px;
        line-height: 20px;
        color: #98A2B3;
    }
}