.btn {
    /**   **/
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 20px;

    /**    **/
    padding: 14px 20px;

    /**   **/
    border-radius: 6px;

    /**   **/
    display: grid;
    grid-auto-flow: column;
    justify-content: center;
    column-gap: 6px;
}

.btn:disabled {
    /**     **/
    cursor: not-allowed;
}

.primary {
    /**   **/
    background-color: #175CD3;

    /**    **/
    color: #FFFFFF;

    /**   **/
    border: none;
}

.primary:disabled {
    /**   **/
    background-color: rgba(23, 92, 211, 0.5);
}

.secondary {
    /**   **/
    background-color: #FFFFFF;

    /**    **/
    color: #175CD3;

    /**   **/
    border: 1px solid #E4E7EC;
}

.secondary:disabled {
    /**   **/
    background-color: #F2F4F7;

    /**    **/
    color: #98A2B3;
}

.kakaoLogin {
    /**   **/
    background-color: #FFE812;

    /**   **/
    border: none;

    /**    **/
    color: #101828;
}

.googleLogin {
    /**   **/
    background-color: #FFFFFF;

    /**   **/
    border: 1px solid #E4E7EC;

    /**    **/
    color: #101828;
}