.container {
    /**    **/
    width: 1366px;

    /**    **/
    margin: 0 auto;
    margin-top: 130px;
    margin-bottom: 90px;

    /**    **/
    padding: 0 103px;

    h2 {
        /**   **/
        font-style: normal;
        font-weight: normal;
        font-size: 16px;
        line-height: 20px;
        color: #101828;

        /**    **/
        margin-bottom: 15px;
    }

    .section {
        /**   **/
        display: grid;
        grid-template-columns: auto;
        justify-content: flex-start;
        row-gap: 10px;

        p {
            /**   **/
            font-style: normal;
            font-weight: normal;
            font-size: 14px;
            line-height: 22px;
            color: #667085;
        }
    }
}