@import '../../../css/common/zIndex';

.overlays {
    /**    **/
    width: 100%;
    height: 100%;

    /**    **/
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    /**   **/
    background-color: rgba(0, 0, 0, 0.3);

    /**    **/
    z-index: $modalZIndex;

    /**     **/
    cursor: pointer;

    .modal {
        /**    **/
        width: 520px;
        height: 494px;

        /**   **/
        background-color: #FFFFFF;

        /**   **/
        border-radius: 6px;

        /**    **/
        position: fixed;
        top: calc(50% - (494px / 2));
        left: calc(50% - (520px / 2));

        /**    **/
        padding: 30px;

        /**   **/
        display: grid;
        row-gap: 34px;

        .titleBar {
            /**   **/
            display: grid;
            grid-template-columns: auto 30px;
            justify-content: space-between;

            h3 {
                /**   **/
                font-style: normal;
                font-weight: bold;
                font-size: 24px;
                line-height: 30px;
                color: #101828;

                /**    **/
                padding: 1px 0;
            }

            button {
                /**    **/
                width: 32px;
                height: 32px;

                /**    **/
                padding: 4px;
            }
        }

        .content {
            /**   **/
            display: grid;
            row-gap: 30px;

            .inputSection {
                /**   **/
                display: grid;
                row-gap: 18px;
            }

            .btnSection {
                /**   **/
                display: grid;
            }
        }
    }
}